import Flex from '../../../StyledComponents/Flex'
import TextContent from '../../../StyledComponents/TextContent'
import React from 'react'
import OnionCode from './OnionCodeCard'

const AreaCodesCard = ({ cardTitle, codes, onClick }) => {
    return (
        <Flex
            direction={'column'}
            align={'center'}
            border={'3px solid white'}
            width={'35%'}
            bRadius={'5px'}
            margin={'8px 0'}
            padding={'10px'}
        >
            <TextContent textAlign={'center'}>{cardTitle}</TextContent>
            <Flex justify={'center'} wrap={'wrap'}>
                {codes.map((code) => (
                    <OnionCode key={code} code={code} onClick={onClick} />
                ))}
            </Flex>
        </Flex>
    )
}

export default AreaCodesCard
