import React, { useEffect, useState } from 'react'
import { slotsRegular } from '../../slots'
import OnionSaturationCard from '../Cards/OnionSaturationCard'
import Flex from '../../../StyledComponents/Flex'
import Title from '../../../StyledComponents/Title'
import Button from '../../../StyledComponents/Button'
import { SelectStyle } from '../../../StyledComponents/Select'
import {
    deselectOnion,
    getSaturationReport,
    selectOnion,
    setPeriodOfReport,
} from '../../../../toolKitRedux/report-slices/saturationSelectedOnionsSlice'
import { useDispatch, useSelector } from 'react-redux'
import OnionCode from '../Cards/OnionCodeCard'
import { kyivCodes, mioCodes, smallCodes } from '../../codes'
import TextContent from '../../../StyledComponents/TextContent'
import AreaCodesCard from '../Cards/AreaCodesCard'

export default React.memo(function SaturationBySelectedOnion() {
    const dispatch = useDispatch()
    const {
        status,
        error,
        periodStart,
        periodEnd,
        kyiv_report,
        mio_report,
        small_report,
        areaCodes,
        selectedOnionCodes,
    } = useSelector((state) => state.selectedOnionsReport)

    const saturationReport = [...kyiv_report, ...mio_report, ...small_report]

    function selectChangeHandler(e) {
        const name = e.target.name
        if (name === 'slotStartPeriodSelector') {
            dispatch(
                setPeriodOfReport({
                    periodStart: e.target.value.substr(0, 2),
                    periodEnd: periodEnd,
                })
            )
        } else if (name === 'slotEndPeriodSelector') {
            dispatch(
                setPeriodOfReport({
                    periodStart: periodStart,
                    periodEnd: e.target.value.substr(0, 2),
                })
            )
        }
    }

    // const onionCodesArray = onionCodes.split(',').map((code) => code.trim())

    // const onionCodes = [[...kyivCodes], [...mioCodes], [...smallCodes]]

    function onionCodesSelectHandler(e) {
        const onionCode = e.target.outerText
        dispatch(selectOnion(onionCode))
    }

    function onionDeselectHandler(e) {
        const onionCode = e.target.outerText
        dispatch(deselectOnion(onionCode))
    }

    function sendRequestForReport() {
        dispatch(
            getSaturationReport({
                onionCodesArray: selectedOnionCodes,
                periodStart: periodStart,
                periodEnd: periodEnd,
            })
        )
    }

    return (
        <Flex direction={'column'} align={'center'} margin={'2em 0 0 0'}>
            <Flex direction={'column'} align={'center'}>
                {areaCodes.map((onionCodesArray, index) => {
                    let title = ''
                    index === 0
                        ? (title = 'Kyiv')
                        : index === 1
                        ? (title = 'MIO')
                        : (title = 'Small')
                    return (
                        <AreaCodesCard
                            key={title}
                            cardTitle={title}
                            codes={onionCodesArray}
                            onClick={onionCodesSelectHandler}
                        />
                    )
                })}
            </Flex>
            <AreaCodesCard
                cardTitle="Selected onions for report"
                codes={selectedOnionCodes}
                onClick={onionDeselectHandler}
            />
            <Flex justify={'center'}>
                <form action="#">
                    <select
                        style={SelectStyle}
                        name="slotStartPeriodSelector"
                        id="1"
                        value={`${periodStart}:00`}
                        onChange={(e) => selectChangeHandler(e)}
                    >
                        {slotsRegular.map((slot, id) => (
                            <option value={slot} key={id}>
                                {slot}
                            </option>
                        ))}
                    </select>
                    <select
                        style={SelectStyle}
                        name="slotEndPeriodSelector"
                        id="2"
                        value={`${periodEnd}:00`}
                        onChange={(e) => selectChangeHandler(e)}
                    >
                        {slotsRegular.map((slot, id) => (
                            <option value={slot} key={id}>
                                {slot}
                            </option>
                        ))}
                    </select>
                    <Button
                        onClick={(e) => {
                            e.preventDefault()
                            sendRequestForReport()
                        }}
                        bcolor={'black'}
                        color={'white'}
                        bradius={'10px'}
                        border={'3px solid white'}
                    >
                        Get report by period
                    </Button>
                </form>
            </Flex>

            <Flex direction={'column'} width={'50%'}>
                <Title
                    fWeight={'800'}
                >{`Апдейт по сатурации с ${periodStart}:00 по ${periodEnd}:00`}</Title>
                <div>
                    {saturationReport.map((onionReport, id) => {
                        if (status === 'resolved') {
                            return (
                                <OnionSaturationCard
                                    {...onionReport}
                                    key={id}
                                />
                            )
                        }
                    })}
                </div>
            </Flex>
            {status === 'loading' && <h2>Loading...</h2>}
            {error && <h2>An error occurred: {error}</h2>}
        </Flex>
    )
})
